import common from '../../../common/common.js'
import uniPopup from '../../../components/uni-popup.vue'

export default {
	data() {
		return {
			name:'aa',
			shopInfo:[],		//用户信息
			scrollLeft:0,
			tabIndex: 0,
			isClickChange:false,
			tabBars: [{
				name: '待配货',
				id: '0'
			}, {
				name: '已配货',
				id: '1'
			}],
			orderList: [],
			popType: 'middle',
			showPopupMiddle: false,
			orderid:0,
			goods_real_weight:0,
			index:'',
			subindex:'',
			order_no:'',
		}
	},
	onLoad() {
		var that = this
		var shopInfo = uni.getStorageSync('shopInfo');
		var utype =  uni.getStorageSync('utype');			//用户类型
		

		if(shopInfo == ''){
			uni.redirectTo({
				url: '/pages/userlogin/userlogin',
			});
			return false
		}
		
		that.getMerchantSuperOrderList();
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');
		
		that.getUserInfo();
		if(that.shopInfo.shop_id > 0){
			that.getMerchantSuperOrderList();
		}
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	methods: {
		
		//展示居中 popup
		showMiddlePopup: function() {
			this.hidePopup();
			this.showPopupMiddle = true;
			//this.resetAttr();
		},
		hidePopup: function() {
			this.showPopupMiddle = false;
			//this.resetAttr();
		},
		
		//输入重量
		setZhong(e){
			var that = this
			that.goods_real_weight = e.detail.value;
			console.log(that.goods_real_weight)
		},
		
		//核销
		hexiao(e){
			var that = this
			var orderno = e.currentTarget.dataset.orderno
			var shopInfo = uni.getStorageSync('shopInfo');
			uni.showModal({
				title:'提示',
				content:'确定要核销吗？',
				success(res) {
					if(res.confirm){
						uni.request({
							url: common.sjApiUrl + 'merchant/hexiaoOrder',
							method: 'GET',
							data: {
								shopid:shopInfo.shop_id,
								orderno:orderno
							},
							success: res => {
								if(res.data.code == 0){
									uni.showToast({
										title:'核销成功',
										icon:'success',
										duration:1500,
										success:res=>{
											setTimeout(function(){
												that.getMerchantSuperOrderList();
											},1500)
										}
									})
								}else{
									uni.showToast({
										title:res.data.msg,
										duration:1500
									})
								}
							},
							fail: () => {},
							complete: () => {}
						});
					}
				}
			})
		},
		//
		jumpUrl(e){
			var that = this
			var url = e.currentTarget.dataset.url
			uni.redirectTo({
				url: url
			});
		},
		
		//获取商户订单列表
		getMerchantSuperOrderList(){
			var that = this
			var uid = uni.getStorageSync('userid');
			var shopInfo = uni.getStorageSync('shopInfo');
			var shop_id = shopInfo.shop_id
			console.log("shop_id",shop_id)
			uni.request({
				url: common.sjApiUrl + 'merchant/getMerchantSuperOrderList',
				method: 'GET',
				data: {
					otype:that.tabIndex,
					shop_id:shop_id
				},
				success: res => {
					console.log("res",res)
					that.orderList = res.data.order_list
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		tapTab(index) { //点击tab-bar
			var that = this
			if (that.tabIndex === index) {
				return false;
			} else {					
				that.isClickChange = true;
				that.tabIndex = index;
				that.getMerchantOrderList();
			}
		},
		
		//更新获取用户信息
		getUserInfo(){
			//console.log('获取用户信息');
			var that = this
			var shopInfo = uni.getStorageSync('shopInfo');
			if(shopInfo == null || shopInfo == ''){
				uni.redirectTo({
					url: '/pages/userlogin/userlogin'
				});
				return false
			}
			
			console.log(shopInfo.mem_id)
			uni.request({
				url: common.sjApiUrl + 'merchant/getUserInfo',
				method: 'GET',
				data: {
					shop_id:shopInfo.shop_id
				},
				success: res => {
					var shop = res.data.userInfo
					console.log(shop)
					if(shop != ''){
						that.shopInfo = shop
						uni.setStorageSync('shopInfo',shop);
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
	},
	components: {
		uniPopup
	}
}
